@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');
* {
 margin: 0;
 padding: 0;
 border: 0;
 outline: 0;
 box-sizing: border-box;
 list-style: none;
 text-decoration: none;
}

:root{
  --color-bg:#381f1f;
  --color-bg-variant: #8b131337;
  --color-primary:grey;
  --color-primary-variant: hsl(#fff);
  --color-white: #ffffff;
  --color-light: rgba(255,255,255,0.6);

--transition: all 400ms ease;

  --contaier-width-lg: 75%;
  --contaier-width-md: 86%;
  --contaier-width-ms: 90%;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/assests/bg-texture.png);
   
}
/* ------------------------------------------------------------------------------------ */

.container{
    width: var(--contaier-width-lg);
    margin: 0 auto;
}
h1,h2,h3,h4,h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
    /* height: 100vh; */
}
section > h2,
section > h5{
    text-align: center;
    color: var(--color-light);
}
section > h2{
    color: var(--color-primary);
}

a{
    color:  var(var(--color-primary));
   transition: var(--transition);
}

a:hover{
    color: var(--color-white);
}

.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}
.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}
 .custom-icon{
    color: grey;
 }
 .custom-icon :hover{
   color: white;
 }
 
/* ............................................................... */
@media screen and (max-width: 1024px) {
    .container{
        width: var(--contaier-width-md);
    }
    section > h2{
        margin-top: 6rem;
    }
}

@media screen and (max-width: 600px) {
    .container{
        width: var(--contaier-width-ms);
    }
    section > h2{
        margin-bottom: 2rem;
    }
}